.cr-header {
  height: 45px;
  background: #1072ce;
  color: #fff;
  align-items: center;
  display: flex;
  padding: 5px 15px;
}

.cr-save-btn {
  color: #fff;
  margin-left: auto;
  order: 2;
  border: 1px solid;
  padding: 0 15px 0 15px;
}

.container-cr {
  width: 24%;
}

::ng-deep .angular-editor-toolbar {
  background-color: #fff !important;
  padding: 0 !important;
}

::ng-deep
  .angular-editor-toolbar
  .angular-editor-toolbar-set
  .angular-editor-button {
  padding: 0 !important;
  border: none !important;
  height: 25px !important;
}

::ng-deep .angular-editor-textarea {
  margin: 0 !important;
  border-top: none !important;
}

::ng-deep .angular-editor-toolbar .angular-editor-toolbar-set {
  margin: 0 !important;
}

.Inner.BorderColor {
  position: relative;
}

span.charLen {
  position: absolute;
  font-size: 10px;
  right: 0;
}

.cr-cros-icon {
  color: #fff;
  font-size: 24px;
  font-weight: bold;
  position: absolute;
  top: 5px;
  right: 14px;
}

.upload-btn {
  padding: 5px 40px;
}

.preview-container {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  margin: 10px 0 15px 0;
}

.attachment-remove-icon {
  color: gray;
  float: right;
  margin: 7px 0;
  font-weight: 700;
  font-size: 20px;
  background: red;
  border-radius: 50%;
  width: 22px;
  height: 22px;
  color: #fff !important;
  display: block;
  font-size: 15px;
  line-height: 19px;
  text-align: center;
  position: absolute;
  right: -5px;
  top: -17px;
}

.clickBtn {
  margin-left: 1rem;
  background: #ff6400;
  color: #fff;
  border: 1px solid #ff6400;
  border-radius: 5px !important;
  min-width: 250px;
  line-height: 0.5;
}
.clickBtn:hover {
  color: #ffffff;
}
.pinfo-consult-btn {
  right: 32% !important;
  position: fixed;
  transform: translate(-32%, 0);
  bottom: 40px;
  min-width: 300px;
}

.saveandclose {
  background: #ff6400;
  border: 1px solid #ff6400;
  color: #fff;
  line-height: 1;
}
.saveandclose:hover {
  background: #ff6400;
  border: 1px solid #ff6400;
  color: #fff;
  line-height: 1;
}
.preview-container > div {
  padding-bottom: 10px;
}
.report-modal .scroll-modal .ql-snow.ql-toolbar button,
.report-modal .scroll-modal .ql-snow .ql-toolbar button {
  padding: 3px;
  width: 21px;
  height: auto !important;
}
.report-modal .scroll-modal .ql-toolbar.ql-snow {
  padding: 0;
  height: 28px;
}
.report-modal .scroll-modal .ql-container {
  max-height: 200px;
  height: 200px;
  overflow-y: auto;
  word-break: break-word;
}

.invalid-comment {
  border: solid 1px #f44336 !important;
}

.input_bg_clr {
  background-color: #ffffff !important;
  color: #000 !important;
  border: 0;
  outline: none;
  /* width: 100%; */
}
.newPrt {
  width: 50%;
}
.newPrt p {
  width: 100% !important;
}
.newPrt p input,
.newPrt input {
  width: 100%;
}
.mat-mdc-option {
  min-height: 38px !important;
}
.uploadBtn {
  background: #0072ce;
  border-radius: 4px;
  color: #fff;
  padding: 5px 20px;
  border: none;
  margin-bottom: 20px;
  margin-top: 15px;
  font-size: 14px;
  font-family: GothamNarrowBold, sans-serif;
}
.imgtext {
  color: #0782d0 !important;
  font-size: 15px;
}
